<template>
  <div id="app">
<el-row :gutter="20">
  <el-pagination @current-change="changePage" :current-page="current_page" :page-size="per_page" layout="total, prev, pager, next, jumper" :total="total">
    </el-pagination>
</el-row>

    <el-row :gutter="30">
  <el-col :span="18" >
         <el-table
    :data="records"
      v-loading.body="loading"
    border
     stripe
    style="width: 100%">
   
     <el-table-column
      prop="hardwareID"
      label="hardwareID"
      fixed
    width="160"
    sortable
      >
      </el-table-column>

     <el-table-column
       inline-template
      prop="timestamp"
      label="timestamp"
      sortable
      >
      <el-popover trigger="hover" placement="top">
        <p>{{ (new Date(row.timestamp*1000)).toGMTString() }}</p>
 
        <div slot="reference">
         {{ (new Date(row.timestamp*1000)).toLocaleString() }}
        </div>
      </el-popover>
      </el-table-column>

 
   <el-table-column
      :prop="col"
      :label="col"
      sortable
      v-for="col in column_show"
      >
      </el-table-column>

    
  </el-table>
  </el-col>
     <el-col :span="6">
       
   
<br>
     Columns:<br><br>
   </el-col>
  <el-col :span="6" >
      <el-checkbox-group v-model="column_show">
        <template v-for="col in column_list">
                <el-checkbox :label="col" ></el-checkbox>
                <br>
        </template>

</el-checkbox-group>
</el-col>
</el-row>




</div>


</template>

<script>
    import Hello from './components/Hello'
    const API_URL = 'http://47.88.212.94/api/query'
    export default {
        name: 'app',
        components: {
            Hello
        },
        data() {
            return {
                tags: [{
                        key: 1,
                        name: 'hardwareID:5832e993aa780',
                        type: ''
                    }, {
                        key: 2,
                        name: 'Event:Error',
                        type: 'danger'
                    }

                ],
                records: [],
                column_show: [],
                column_list: ['eventID', 'lat', 'lng', 'alt', 'sats', 'gX', 'gY', 'gZ', 'yaw', 'pitch', 'roll', 'totalEngineHours', 'totalFuelConsumption', 'currentFuelRate', 'totalIdleFuel', 'totalIdleHour', 'engineRPM', 'oilTemperature', 'coolantTemperature', 'vehicleSpeed', 'gearSelection'],
                filters: [],
                loading: true,
                total: 0,
                per_page: 10,
                current_page: 1
            }
        },
        mounted() {
            console.log(222)
            this.fetchData(this.filters)
        },
        methods: {
            changePage(p) {
                this.current_page = p
                this.fetchData(this.filters)
            },
            fetchData(filters) {
                console.log('Fetching=>>>>')
                var self = this
                this.loading = true
                this.$http.post(API_URL + '?page=' + this.current_page, {
                    filters
                }).then(this.receiveData, function() {
                    this.loading = false
                });

            },
            receiveData(xhr) {
                this.records = xhr.data.data
                console.log(xhr)
                    //this.pagination = xhr.data.
                this.total = xhr.data.total
                this.per_page = xhr.data.per_page
                this.loading = false

            }
        }
    }
</script>


